<template>
  <div><a href="/">© 2022 兔子二维码 tuzim.net</a></div>
  <div class="share bounce-in" v-if="title">
    <ShareNetwork network="Weibo" :url="url" :title="title" style="background: #e6142c">
      <WeiboOutlined />
      <span>分享到 微博</span>
    </ShareNetwork>
    <ShareNetwork network="Twitter" :url="url" :title="title" style="background: #1d9bf0">
      <TwitterOutlined />
      <span>分享到 Twitter</span>
    </ShareNetwork>
  </div>
</template>

<script>
import { reactive, toRefs, watch } from 'vue';
import { WeiboOutlined, TwitterOutlined } from '@ant-design/icons-vue';
import { useRoute } from 'vue-router';

export default {
  name: 'GlobalFooter',
  components: { WeiboOutlined, TwitterOutlined },
  setup() {
    let view = reactive({
      title: '',
      url: ''
    });

    let route = useRoute();
    watch(
      () => route.path,
      () => {
        view.title = '';
        view.url = '';
        setTimeout(() => {
          view.title = document.title;
          view.url = window.location.href;
        }, 1000);
      },
      {
        immediate: true
      }
    );

    return {
      ...toRefs(view)
    };
  }
};
</script>

<style lang="less" scoped>
.share {
  position: fixed;
  right: 10px;
  top: 80px;
  display: flex;
  flex-direction: column;
  width: 34px;
  transition: all 0.3s;
  &:hover {
    width: 150px;
  }
  a[class^='share-network-'] {
    display: flex;
    height: 34px;
    line-height: 34px;
    margin-top: 10px;
    color: #ffffff;
    border-radius: 5px;
    overflow: hidden;
  }
  a[class^='share-network-'] .anticon {
    background-color: rgba(0, 0, 0, 0.2);
    padding: 5px;
    font-size: 24px;
    flex: 0 1 auto;
  }
  a[class^='share-network-'] span {
    white-space: nowrap;
    flex: 1 1 0%;
    font-weight: 500;
  }
}
</style>
